<template>
	<button class="btn btn-primary btn-sm" @click="callback">{{btnText}}</button>
</template>
<script>
	export default {
		props: {
			btnText: {
				type: String,
				default: 'Button'
			},
			callback: {
				type: Function,
				default: function () {}
			}
		}
	}
</script>


